import md5 from 'helpers/md5'

const setter = (key) => ({state}, value) => state[key] = value
export default {
    set_parse_pages_result : setter("parse_pages_result"),

    updateFiles : ({state, commit}, {type, ...payload}) => {
        const oldFiles = state.files

         switch(type){
            case 'visibility':
                for(let elem of oldFiles)
                    if(elem.id == payload.id)
                    {
                        elem.visible = payload.visible
                    } 
                return
            case 'set':
                state.files = payload.files
                return 
            case 'remove':
                state.files = oldFiles.filter(elem => elem.id != payload.id )
                return 
            case 'onload':
                for(let elem of oldFiles)
                    if(elem.id == payload.file.id)
                    {
                        elem.preview = payload.file.reader.result
                        elem.loaded = true
                    } 
                return
            case 'onprogress':
                for(let elem of oldFiles)
                    if(elem.id == payload.file.id)
                    {
                        elem.progress = payload.progress
                    } 

                return
            case 'add':
                const filteredFiles = payload.files.filter((file) => file.type == 'image/jpeg' || file.type == 'image/png')
                if(filteredFiles.length != payload.files.length)
                    payload.raiseAlert({type: 'warning', text: 'Файлы, не являющиеся изображениями проигнорированы'})               
                state.files = oldFiles.concat(
                    filteredFiles.map((file) => {
                        const result = {
                            file,
                            progress : 0,
                            loaded: false,
                            preview : null,
                            reader : new FileReader(),
                            visible : true,
                            id: md5('' + file.path + new Date()),
                        }

                        result.reader.onload = function(){
                            payload.updateFiles({type: 'onload', file: result})
                        };
                        
                        result.reader.onprogress = function(event){

                            payload.updateFiles({type: 'onprogress', file: result, progress : event.loaded / event.total})
                        };

                        result.reader.readAsDataURL(file);
                        return result
                    })
                )
                return 
        }
    },
    SET_WAITING_TICKET : setter('waitingTicket'),
}